@media (min-width: 961px) and (max-width: 1025px) {
	.loading-clockwise {
		margin-left: 27em;
	}

	/* When network error */
	.network-error-boundary {
		margin-left: 18em;
	}

	.NavbarItems {
		position: relative;
	}

	.nav-menu {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 90vh;
		position: absolute;
		top: 80px;
		left: -100%;
		opacity: 1;
		transition: all 0.5s ease;
	}

	.nav-menu.active {
		background: #242222;
		left: 0;
		opacity: 1;
		transition: all 0.5s ease;
		z-index: 1;
		padding-top: 2em;
		height: 20em;
		padding-left: 0em;

	}

	.nav-links {
		text-align: center;
		padding: 2rem;
		width: 100%;
		display: table;
	}

	.nav-links:hover {
		background-color: #1888ff;
		border-radius: 0;
	}

	.navbar-logo {
		position: absolute;
		top: -12px;
		left: 100px;
		-webkit-transform: translate(25%, 50%);
		        transform: translate(25%, 50%);
	}

	.navbar-logo img {
		margin-top: -20px;
		margin-left: -68px;
	}

	.menu-icon {
		display: block;
		position: absolute;
		top: 0;
		left: 45px;
		-webkit-transform: translate(-100%, 60%);
		        transform: translate(-100%, 60%);
		font-size: 1.8rem;
		cursor: pointer;
	}

	.fa-times {
		color: #fff;
		font-size: 2rem;
	}

	.nav-links-mobile {
		display: block;
		text-align: center;
		padding: 1.5rem;
		margin: 2rem auto;
		border-radius: 4px;
		width: 80%;
		background: #1888ff;
		text-decoration: none;
		color: #fff;
		font-size: 1.5rem;
	}

	.nav-links-mobile:hover {
		background: #fff;
		color: #1888ff;
		transition: 250ms;
	}

	button {
		display: none;
	}

	.current-user {
		display: none !important;
	}

	.user-null {
		display: none !important;
	}

	.current-user-mobile {
		display: flex;
		position: absolute;
		top: 11px;
		right: 1em;
		margin-right: 2em;
	}

	.current-user-mobile .sign-out-btn button {
		margin-top: 0px;
		margin-left: 0px;
	}

	.no-user-mobile .login-nav-btn {
		display: flex;
		position: absolute;
		right: 1em;
		top: 28px;
	}

	.login-nav-btn span {
		color: cornsilk;
		font-family: monospace;
	}

	/* User Profile */
	.user-profile-container form {
		width: 36em;
	}

	/* SearchBar elements to.. */
	.mobile-search-app {
		display: flex;
		margin-left: -25em;
		margin-top: -9px;
	}

	.search-app {
		display: none !important;
	}

	.search-app .search {
		display: flex;
		margin-top: -18em;
	}

	.search-inputs input {
		width: 30em;
		margin-left: 23em;
	}

	.search-icon {
		margin-left: 41em;
	}

	.search-results {
		width: 479px;
		margin-left: 368px;
		margin-top: 1px;
		z-index: 2;
	}

	/* Card styles */
	.container-fluid {
		margin-left: 0em;
	}

	.container-fluid .row {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		margin-left: 1em !important;
		margin-top: -45em !important;
	}

	.card {
		width: 199px !important;
		margin-top: 0 !important;
		margin-bottom: 2em !important;
		margin-left: -2em !important;
	}


	/* View Page Contents */
	.content {
		width: 36em !important;
		margin-left: 1em !important;
		margin-top: 3em !important;
	}

	.location-info {
		width: 20em;
		padding-top: 2em !important;
		margin-left: 1em !important;
	}

	.recommended-products {
		margin-left: 1em !important;
		margin-top: -33em !important;
	}

	/* My destinations */
	.text-centre {
		margin-left: 6em;
	}

	/* Quick links */
	.about {
		margin-left: 0em;
		width: 100%;
	}

	/* Coming Soon */
	.coming-soon {
		margin-top: 9em;
		margin-bottom: 10em;
		margin-left: 18em;
	}

	.coming-soon h1 {
		margin-left: 0px;
	}

	.coming-soon p {
		margin-left: 0em;
		text-align: center;
	}

	/* Footer element */
	footer {
		width: 100%;
	}

	footer .container {
		margin-left: 0em;
	}

	footer p {
		width: 100%;
	}

	.aboutus {
		display: flex;
	}

	.quickLinks {
		margin-left: 40px;
	}

	.contactBody {
		margin-left: -74px;
	}

	.copyrightText {
		width: 100%;
	}
}


