		display: grid;
		grid-template-columns: repeat(3, 1fr);
	}

	.card {
		width: 232px !important;
		height: 33em !important;
		margin-left: -33px !important;
	}

	/* View Page Content */
	.content {
		margin-left: 1em !important;
		margin-top: 2em !important;
		width: 49em !important;
	}

	.location-info {
		margin-left: 1em !important;
		margin-bottom: 26em !important;
	}

	.recommended-products {
		margin-left: 1em !important;
	}

	/* My destinations */
	.text-centre {
		margin-left: 6em;
	}

	/* Coming Soon */
	.coming-soon {
		margin-top: 9em;
		margin-bottom: 10em;
		margin-left: 18em;
	}

	.coming-soon h1 {
		margin-left: 0px;
	}

	.coming-soon p {
		margin-left: 0em;
		text-align: center;
	}

	/* Footer */
	footer {
		width: 100%;
	}

	.contact {
		margin-left: -3em;
	}

	.copyrightText {
		width: 100%;
	}

	.quickLinks {
		margin-left: 3em;
	}

}


@media (min-width:1201px) and (max-width:1280px) {
	.loading-clockwise {
		margin-left: 35em;
	}

	.nav-menu {
		width: 24vw;
	}

	.user-null {
		padding-left: 4px;
	}

	.current-user {
		margin-left: 20px;
	}

	.current-user button {
		margin-left: 215px;
	}

	.current-user .sign-out-btn {
		margin-left: 40px;
	}

	/* User Profile */
	.user-profile-container form {
		width: 50em;
	}


	/* Search Component */
	.mobile-search-app {
		display: none !important;
	}

	.search-inputs input {
		width: 16em;
	}

	.search-icon {
		margin-left: 11em;
	}

	.search-results {
		width: 255px;
		margin-left: 16px;
	}

	/* Card Styles */
	.card {
		width: 230px !important;
	}

}


@media (min-width:1281px) and (max-width:1350px) {
	.loading-clockwise {
