	}

	.navbar-logo {
		margin-left: -1em;
	}

	.user-profile-container form {
		width: 50em;
	}

	.mobile-search-app {
		display: none !important;
	}

	.card {
		width: 245px !important;
	}

}


@media (min-width:1367px) and (max-width:1440px) {
	.mobile-search-app {
		display: none !important;
	}

	.search-results {
		width: 382px;
	}
}
.network-error-boundary{
    margin-top: -36em;
    margin-bottom: 33em;
    margin-left: 33em;
}

